.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg.jpg);
  background-position: center;
  background-size: cover;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100vh;
  }

  &__bottom {
    position: relative;

    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 65px;
    }
  }

  &--title {
    margin: 0;
    font-size: 36px;
    line-height: 100%;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 56px;
    }
  }
}
